import React, { useState } from 'react';
import { Section, Animation } from 'gatsby-theme-portfolio-minimal';

const CoursesSection = () => {
  const [selectedTerm, setSelectedTerm] = useState(null);

  const handleClick = (term) => {
    if (selectedTerm === term) {
      setSelectedTerm(null);
    } else {
      setSelectedTerm(term);
    }
  };

  const renderCourses = (term) => {
    if (selectedTerm !== term.term) return null;

    return (
      <div className="courses-grid">
        {term.courses.map((course, index) => (
          <div key={index} className="course">
            <h4>&emsp;&emsp;{course.title}</h4>
            <p>&emsp;&emsp;&emsp;&emsp;{course.technology}</p>
          </div>
        ))}
      </div>
    );
  };
  const courses = [  {    term: 'Term 1',    courses: [      { title: 'COMM 1116 - Business Communications 1', technology: 'Communication Skills' },      { title: 'COMP 1100 - CST Program Fundamentals', technology: 'Programming Basics' },      { title: 'COMP 1113 - Applied Mathematics', technology: 'Mathematics for Computing' },      { title: 'COMP 1510 - Programming Methods', technology: 'Python, Problem Solving' },      { title: 'COMP 1537 - Web Development 1', technology: 'HTML, CSS, JavaScript' },      { title: 'COMP 1712 - Business Analysis and System Design', technology: 'Business Analysis, UML' },      { title: 'COMP 1800 - Projects 1', technology: 'Software Development Projects' },    ],
  },
  {
    term: 'Term 2',
    courses: [
      { title: 'COMM 2216 - Business Communications 2', technology: 'Communication Skills' },
      { title: 'COMP 2121 - Discrete Mathematics', technology: 'Discrete Mathematics' },
      { title: 'COMP 2510 - Procedural Programming', technology: 'C Programming' },
      { title: 'COMP 2522 - Object Oriented Programming 1', technology: 'Java, OOP' },
      { title: 'COMP 2714 - Relational Database Systems', technology: 'SQL, Database Design' },
      { title: 'COMP 2721 - Computer Organization/Architecture', technology: 'Computer Hardware, Assembly Language' },
      { title: 'COMP 2990 - Cooperative Education Workterm 1', technology: 'Cooperative Education' },
    ],
  },
  {
    term: 'Term 3',
    courses: [
      { title: 'COMP 3522 - Object Oriented Programming 2', technology: 'Python, Design Patterns, OOP' },
      { title: 'COMP 3717 - Mobile Development with Android Technologies*', technology: 'Kotlin, Android, Mobile App Development' },
      { title: 'COMP 3721 - Introduction to Data Communications', technology: 'Networking, TCP/IP' },
      { title: 'COMP 3760 - Algorithm Analysis and Design', technology: 'Algorithms, Data Structures' },
      { title: 'MATH 3042 - Applied Probability and Statistics', technology: 'Probability, Statistics' },
      { title: 'COMP 3800 - Projects Practicum 1**', technology: 'Software Development Projects' },
      { title: 'COMP 3990 - Cooperative Education Workterm 2', technology: 'Cooperative Education' },
    ],
  },
  {
    term: 'Term 4',
    courses: [
      { title: 'COMP 3962 - Cloud Computing Platforms', technology: 'AWS, Google Cloud, Azure' },
      { title: 'COMP 4537 - Internet Software Architecture*', technology: 'Internet Software, Architecture' },
      { title: 'COMP 4736 - Introduction to Operating Systems', technology: 'Operating Systems' },
      { title: 'LIBS 7102 - Ethics for Computing Professionals*', technology: 'Ethics, IT Professionalism' },
      { title: 'COMP 4964 - DevOps Engineering', technology: 'DevOps' },
      { title: 'COMP 4968 - Serverless Computing and Microservices', technology: 'Serverless, Microservices' },
    ],
  },
];

return (
    <Animation type="fadeUp">
      <Section anchor="courses" heading="Courses">
        <div className="terms-grid">
          {courses.map((term, index) => (
            <React.Fragment key={index}>
              <div className="term" onClick={() => handleClick(term.term)}>
                <h3>{term.term} {selectedTerm === term.term ? '▼' : '►'}</h3>
              </div>
              {selectedTerm === term.term && renderCourses(term)}
            </React.Fragment>
          ))}
        </div>
      </Section>
    </Animation>
  );
};

export default CoursesSection;